	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"id":"C:/Users/mcore/Source/Repos/wavefunctionp.github.io/src/posts/firstPost.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"First!","date":"2017-12-14T00:00:00.000Z"},"html":"<p>Wow! I haven't blogged in years. Since about 2005, I believe. My old blog is now long gone. Maybe one day I'll resurect those <a href=\"https://web.archive.org/web/*/http://mctphysics.com/\">old posts</a> from the internet archives. Not that I think there is much value in it. However, I do believe it has posts about my first attempts at javascript. who knows.</p>\n<p>I have completed the transition of my blog over to <a href=\"https://www.gatsbyjs.org/\">Gatsby</a>, and one of the reasons I chose gastby is that I can use my normal React workflow, but also staticly render all the assets for very fast load times. I had been using manual html files with html imports to handle things like headers and templating.</p>\n<p>While HTML imports worked fine for the most part, I just couldn't see myself doing much more than a very basic website with it. It doesn't seem like HTML imports is really going to catch on any way (probably for good reason).</p>\n<p>I knew that I didn't want to tie myself to Wordpress, which would have required me to get a domain name or link away from my github profile page. Nevermind the issues with security.</p>\n<p>And if I was going to go with a sever rendered site, it would probably be ASP.net on Azure.</p>\n<p>There was also <a href=\"https://jekyllrb.com/\">Jekyll</a> (I misspell it every time it seems), which is what <a href=\"https://help.github.com/articles/using-jekyll-as-a-static-site-generator-with-github-pages/\">GitHub Pages</a> recommends, but I didn't want to hitch myself to the Ruby wagon. I already have enough tools to keep track of.</p>\n<p>But Gatsby...Gatsby has worked like a charm. I am very happy with the results so far. I am hopeful that this will work out in the longer term. I've had tremendous fun putting it together. And maybe this can be an outlet for my many random rants instead of them being squirreled away in random social media posts.</p>\n<p>Time will tell.</p>"}}]}},"pathContext":{}}

